        this.total_reporters += v['reporter_count'];
        this.total_pages += v['page_count'];
        if (k === 'us') {
          this.federal_cases = v['case_count'];
          this.federal_reporters = v['reporter_count'];
          this.federal_pages = v['page_count'];
        }
      }
    },
    mounted() {
